import request from "../../utils/request";

const types = {};

const state = {};

const getters = {};

const actions = {
  getAnswers({ commit }, id) {
    return new Promise((resolve, reject) => {
      request
        .get("/exams", { params: { id: id } })
        .then(res => {
          if (res.data.error === false) {
            resolve(res);
          } else {
            reject(res);
          }
        })
        .catch(err => {
          reject(err.response);
        });
    });
  },
  submitAnswer({ commit }, data) {
    return new Promise((resolve, reject) => {
      request
        .post("/exams", {
          id: data.id,
          answers: JSON.stringify(data.answers)
        })
        .then(res => {
          if (res.data.error === false) {
            resolve(res);
          } else {
            reject(res);
          }
        })
        .catch(err => {
          reject(err.response);
        });
    });
  }
};

const mutations = {};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
