import Vue from "vue";
import store from "../store";
import request from "../utils/request";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: () => import("../views/front/Exams.vue"),
    meta: {
      requireAuth: true,
      requireNotAdmin: true
    }
  },
  {
    path: "/user/:path",
    name: "User",
    component: () => import("../views/front/User.vue"),
    meta: {
      requireAuth: true
    }
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/Login.vue"),
    meta: {
      requireGuest: true
    }
  },
  {
    path: "/register",
    name: "Register",
    component: () => import("../views/Register.vue"),
    meta: {
      requireGuest: true
    }
  },
  {
    path: "/exams",
    name: "Exams",
    component: () => import("../views/front/Exams.vue"),
    meta: {
      requireAuth: true,
      requireNotAdmin: true
    }
  },
  {
    path: "/answer/:id",
    name: "Answer",
    component: () => import("../views/front/Answer.vue"),
    meta: {
      requireAuth: true,
      requireNotAdmin: true
    }
  },
  {
    path: "/rank",
    name: "Rank",
    component: () => import("../views/front/Rank.vue"),
    meta: {
      requireAuth: true,
      requireNotAdmin: true
    }
  },
  {
    path: "/admin",
    name: "Admin",
    component: () => import("../views/back/Index.vue"),
    meta: {
      requireAdmin: true
    },
    children: [
      {
        path: "",
        name: "Admin-Index",
        component: () => import("../components/back/Users.vue"),
        meta: {
          requireAdmin: true
        }
      },
      {
        path: "users",
        name: "Admin-Users",
        component: () => import("../components/back/Users.vue"),
        meta: {
          requireAdmin: true
        }
      },
      {
        path: "exams",
        name: "Admin-Exams",
        component: () => import("../components/back/Exams.vue"),
        meta: {
          requireAdmin: true
        }
      },
      {
        path: "questions",
        name: "Admin-Questions",
        component: () => import("../components/back/Questions.vue"),
        meta: {
          requireAdmin: true
        }
      }
    ]
  },
  {
    path: "*",
    redirect: "/"
  }
];

const router = new VueRouter({
  routes
});

// 路由守卫
router.beforeEach((to, from, next) => {
  if (
    to.meta.requireAuth ||
    to.meta.requireGuest ||
    to.meta.requireAdmin ||
    to.meta.requireNotAdmin
  ) {
    if (store.state.user.user === false) {
      request
        .get("/users")
        .then(res => {
          if (res.data.error === false) {
            store.commit("user/SET_USER", res.data.data);
            if (
              to.meta.requireNotAdmin &&
              store.state.user.user.role === "admin"
            ) {
              next({ path: "/admin" });
            } else if (
              to.meta.requireGuest ||
              (to.meta.requireAdmin && store.state.user.user.role !== "admin")
            ) {
              next({ path: "/" });
            } else {
              next({ path: from.query.redirect });
            }
          } else {
            if (to.meta.requireGuest) {
              next();
            } else {
              next({
                path: "/login",
                query: { redirect: to.fullPath }
              });
            }
          }
        })
        .catch(err => {
          if (to.meta.requireGuest) {
            next();
          } else {
            next({
              path: "/login",
              query: { redirect: to.fullPath }
            });
          }
        });
    } else {
      if (to.meta.requireNotAdmin && store.state.user.user.role === "admin") {
        next({ path: "/admin" });
      } else if (
        to.meta.requireGuest ||
        (to.meta.requireAdmin && store.state.user.user.role !== "admin")
      ) {
        next({ path: "/" });
      } else {
        next();
      }
    }
  } else {
    next();
  }
});

export default router;
