import request from "../../utils/request";

const types = {};

const state = {};

const getters = {};

const actions = {
  getExams({ commit }, data) {
    return new Promise((resolve, reject) => {
      request
        .get("/admin/exams", { params: data })
        .then(res => {
          if (res.data.error === false) {
            resolve(res);
          } else {
            reject(res);
          }
        })
        .catch(err => {
          reject(err.response);
        });
    });
  },
  deleteExam({ commit }, id) {
    return new Promise((resolve, reject) => {
      request
        .delete("/admin/exams", {
          data: {
            id: id
          }
        })
        .then(res => {
          if (res.data.error === false) {
            resolve(res);
          } else {
            reject(res);
          }
        })
        .catch(err => {
          reject(err.response);
        });
    });
  },
  editExam({ commit }, data) {
    return new Promise((resolve, reject) => {
      request
        .put("/admin/exams", data)
        .then(res => {
          if (res.data.error === false) {
            resolve(res);
          } else {
            reject(res);
          }
        })
        .catch(err => {
          reject(err.response);
        });
    });
  },
  searchExam({ commit }, data) {
    return new Promise((resolve, reject) => {
      request
        .post("/admin/exam", data)
        .then(res => {
          if (res.data.error === false) {
            resolve(res);
          } else {
            reject(res);
          }
        })
        .catch(err => {
          reject(err.response);
        });
    });
  },
  createExam({ commit }, data) {
    return new Promise((resolve, reject) => {
      request
        .post("/admin/exams", data)
        .then(res => {
          if (res.data.error === false) {
            resolve(res);
          } else {
            reject(res);
          }
        })
        .catch(err => {
          reject(err.response);
        });
    });
  }
};

const mutations = {};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
