import request from "../../utils/request";

const types = {};

const state = {};

const getters = {};

const actions = {
  getRank({ commit }) {
    return new Promise((resolve, reject) => {
      request
        .get("/rank")
        .then(res => {
          if (res.data.error === false) {
            resolve(res);
          } else {
            reject(res);
          }
        })
        .catch(err => {
          reject(err.response);
        });
    });
  }
};

const mutations = {};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
